//menu scroll
function bindOnScrollEvent() {
  $(document).off("scroll").on("scroll", function () {
    window.innerWidth > 0 && (
      addOrRemoveFillBg(),
      $(window).scrollTop() > 200 ? $(".scroll-top-wrapper").addClass("show") : $(".scroll-top-wrapper").removeClass("show")
    )
  })
}
function addOrRemoveFillBg() {
  $(window).scrollTop() > 0 ? (
    $(".main-header").removeClass("bg-trans").addClass("bg-white")
  ) : (
      $(".main-header").removeClass("bg-white").addClass("bg-trans")
    )
}
function scrollToTop() {
  verticalOffset = "undefined" != typeof verticalOffset ? verticalOffset : 0,
    element = $("body"),
    offset = element.offset(),
    offsetTop = offset.top,
    $("html,body").animate({ scrollTop: offsetTop }, 500, "linear")
}
  bindOnScrollEvent(),
  $(window).resize(function () {
    bindOnScrollEvent(),
      window.innerWidth > 991 && addOrRemoveFillBg()
  });

//circular progressbar
;
(function ($) {
	$.fn.loading = function () {
		var DEFAULTS = {
			backgroundColor: '#b3cef6',
			progressColor: '#4b86db',
			percent: 0,
			price: '',
			duration: 2000
		};	
		
		$(this).each(function () {
			var $target  = $(this);

			var opts = {
			backgroundColor: $target.data('color') ? $target.data('color').split(',')[0] : DEFAULTS.backgroundColor,
			progressColor: $target.data('color') ? $target.data('color').split(',')[1] : DEFAULTS.progressColor,
			percent: $target.data('percent') ? $target.data('percent') : DEFAULTS.percent,
			price: $target.data('price') ? $target.data('price') : DEFAULTS.price,
			duration: $target.data('duration') ? $target.data('duration') : DEFAULTS.duration
			};
			// console.log(opts);
			if(!$target.data('price')){
				$target.append('<div class="background"></div><div class="center-content"><span class="counter">' + opts.percent + '%</span></div><div class="rotate"></div><div class="left"></div><div class="right"></div><div class="rotate-only"><span></span></div>');
			}else{
				$target.append('<div class="background"></div><div class="center-content"><span class="price">' + opts.price + '</span><span class="counter">' + opts.percent + '%</span></div><div class="rotate"></div><div class="left"></div><div class="right"></div><div class="rotate-only"><span></span></div>');
			}
	
			$target.find('.background').css('background-color', opts.backgroundColor);
			$target.find('.left').css('background-color', opts.backgroundColor);
			$target.find('.rotate').css('background-color', opts.progressColor);
			$target.find('.right').css('background-color', opts.progressColor);
	
			var $rotate = $target.find('.rotate');
			var $rotateOnly = $target.find('.rotate-only');
			setTimeout(function () {	
				$rotate.css({
					'transition': 'transform ' + opts.duration + 'ms linear',
					'transform': 'rotate(' + opts.percent * 3.6 + 'deg)'
				});
				$rotateOnly.css({
					'transform': 'rotate(' + opts.percent * 3.6 + 'deg)'
				});
			},1);		

			if (opts.percent > 50) {
				var animationRight = 'toggle ' + (opts.duration / opts.percent * 50) + 'ms step-end';
				var animationLeft = 'toggle ' + (opts.duration / opts.percent * 50) + 'ms step-start';  
				$target.find('.right').css({
					animation: animationRight,
					opacity: 1
				});
				$target.find('.left').css({
					animation: animationLeft,
					opacity: 0
				});
			}
		});
	}
})(jQuery);


